import React from "react";

const Resume = ({ data }) => {
  if (data) {
    var skillmessage = data.skillmessage;
    var education = data.education.map(function (education) {
      return (
        <div key={education.school}>
          <h3>{education.school}</h3>
          <p className="info">
            {education.degree} <span>&bull;</span>
            <em className="date">{education.graduated}</em>
          </p>
          <p>{education.description}</p>
        </div>
      );
    });
    var work = data.work.map(function (work) {
      return (
        <div key={work.company}>
          <h3>{work.company}</h3>
          <p className="info">
            {work.title}
            <span>&bull;</span> <em className="date">{work.years}</em>
          </p>
          <p>{work.description}</p>
        </div>
      );
    });
    var skills = data.skills.map(function (skills) {
      return (
        <div key={skills.category}>
          {/* <span style={{ width: skills.level }} className={className}></span> */}
          <h1>{skills.category}</h1>
          <p>{skills.items}</p>
        </div>
      );
    });
    var languages = data.languages.map(function (languages) {
      var className = "bar-expand " + languages.name.toLowerCase();
      return (
        <li key={languages.name}>
          <span style={{ width: languages.level }} className={className}></span>
          <em>{languages.name}</em>
        </li>
      );
    });
  }

  return (
    <section id="resume">
      <div className="row education">
        <div className="three columns header-col">
          <h1>
            <span>Education</span>
          </h1>
        </div>

        <div className="nine columns main-col">
          <div className="row item">
            <div className="twelve columns">{education}</div>
          </div>
        </div>
      </div>

      <div className="row work">
        <div className="three columns header-col">
          <h1>
            <span>Work</span>
          </h1>
        </div>

        <div className="nine columns main-col">{work}</div>
      </div>

      <div className="row skills">
        <div className="three columns header-col">
          <h1>
            <span>Skills</span>
          </h1>
        </div>

        <div className="nine columns main-col">{skills}</div>
      </div>

      <div className="row language">
        <div className="three columns header-col">
          <h1>
            <span>Languages</span>
          </h1>
        </div>

        <div className="nine columns main-col">
          <div className="bars">
            <ul className="languages">{languages}</ul>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Resume;
